import { createRouter, createWebHashHistory } from 'vue-router'
import index from '../views'
import home from '../views/pages/home.vue'
import login from '../views/pages/login.vue'
import newsList from '@/views/pages/news/list'
import xibugaikuangList from '@/views/pages/xibugaikuang/list'
import xibugaikuangDetail from '@/views/pages/xibugaikuang/formModel'
import xibugaikuangAdd from '@/views/pages/xibugaikuang/formAdd'
import jiaoxuexinxiList from '@/views/pages/jiaoxuexinxi/list'
import jiaoxuexinxiDetail from '@/views/pages/jiaoxuexinxi/formModel'
import jiaoxuexinxiAdd from '@/views/pages/jiaoxuexinxi/formAdd'
import keyanxinxiList from '@/views/pages/keyanxinxi/list'
import keyanxinxiDetail from '@/views/pages/keyanxinxi/formModel'
import keyanxinxiAdd from '@/views/pages/keyanxinxi/formAdd'
import dangjiangongzuoList from '@/views/pages/dangjiangongzuo/list'
import dangjiangongzuoDetail from '@/views/pages/dangjiangongzuo/formModel'
import dangjiangongzuoAdd from '@/views/pages/dangjiangongzuo/formAdd'
import zhaoshengxinxiList from '@/views/pages/zhaoshengxinxi/list'
import zhaoshengxinxiDetail from '@/views/pages/zhaoshengxinxi/formModel'
import zhaoshengxinxiAdd from '@/views/pages/zhaoshengxinxi/formAdd'
import jiaoshifengcaiList from '@/views/pages/jiaoshifengcai/list'
import jiaoshifengcaiDetail from '@/views/pages/jiaoshifengcai/formModel'
import jiaoshifengcaiAdd from '@/views/pages/jiaoshifengcai/formAdd'
import jiuyexinxiList from '@/views/pages/jiuyexinxi/list'
import jiuyexinxiDetail from '@/views/pages/jiuyexinxi/formModel'
import jiuyexinxiAdd from '@/views/pages/jiuyexinxi/formAdd'
import storeupList from '@/views/pages/storeup/list'
import zhiduxinxiList from '@/views/pages/zhiduxinxi/list'
import zhiduxinxiDetail from '@/views/pages/zhiduxinxi/formModel'
import zhiduxinxiAdd from '@/views/pages/zhiduxinxi/formAdd'
import jinengjingsaiList from '@/views/pages/jinengjingsai/list'
import jinengjingsaiDetail from '@/views/pages/jinengjingsai/formModel'
import jinengjingsaiAdd from '@/views/pages/jinengjingsai/formAdd'

const routes = [{
		path: '/',
		redirect: '/index/home'
	},
	{
		path: '/index',
		component: index,
		children: [{
			path: 'home',
			component: home
		}
		, {
			path: 'newsList',
			component: newsList
		}
		, {
			path: 'xibugaikuangList',
			component: xibugaikuangList
		}, {
			path: 'xibugaikuangDetail',
			component: xibugaikuangDetail
		}, {
			path: 'xibugaikuangAdd',
			component: xibugaikuangAdd
		}
		, {
			path: 'jiaoxuexinxiList',
			component: jiaoxuexinxiList
		}, {
			path: 'jiaoxuexinxiDetail',
			component: jiaoxuexinxiDetail
		}, {
			path: 'jiaoxuexinxiAdd',
			component: jiaoxuexinxiAdd
		}
		, {
			path: 'keyanxinxiList',
			component: keyanxinxiList
		}, {
			path: 'keyanxinxiDetail',
			component: keyanxinxiDetail
		}, {
			path: 'keyanxinxiAdd',
			component: keyanxinxiAdd
		}
		, {
			path: 'dangjiangongzuoList',
			component: dangjiangongzuoList
		}, {
			path: 'dangjiangongzuoDetail',
			component: dangjiangongzuoDetail
		}, {
			path: 'dangjiangongzuoAdd',
			component: dangjiangongzuoAdd
		}
		, {
			path: 'zhaoshengxinxiList',
			component: zhaoshengxinxiList
		}, {
			path: 'zhaoshengxinxiDetail',
			component: zhaoshengxinxiDetail
		}, {
			path: 'zhaoshengxinxiAdd',
			component: zhaoshengxinxiAdd
		}
		, {
			path: 'jiaoshifengcaiList',
			component: jiaoshifengcaiList
		}, {
			path: 'jiaoshifengcaiDetail',
			component: jiaoshifengcaiDetail
		}, {
			path: 'jiaoshifengcaiAdd',
			component: jiaoshifengcaiAdd
		}
		, {
			path: 'jiuyexinxiList',
			component: jiuyexinxiList
		}, {
			path: 'jiuyexinxiDetail',
			component: jiuyexinxiDetail
		}, {
			path: 'jiuyexinxiAdd',
			component: jiuyexinxiAdd
		}
		, {
			path: 'storeupList',
			component: storeupList
		}
		, {
			path: 'zhiduxinxiList',
			component: zhiduxinxiList
		}, {
			path: 'zhiduxinxiDetail',
			component: zhiduxinxiDetail
		}, {
			path: 'zhiduxinxiAdd',
			component: zhiduxinxiAdd
		}
		, {
			path: 'jinengjingsaiList',
			component: jinengjingsaiList
		}, {
			path: 'jinengjingsaiDetail',
			component: jinengjingsaiDetail
		}, {
			path: 'jinengjingsaiAdd',
			component: jinengjingsaiAdd
		}
		]
	},
	{
		path: '/login',
		component: login
	}
]

const router = createRouter({
  history: createWebHashHistory(process.env.BASE_URL),
  routes
})

export default router
